/* eslint-disable @typescript-eslint/no-unused-vars */
import themeCssStyle from "../style/theme.css" with { type: "css" };
import componentCssStyle from "../style/class/ControlBox.css" with { type: "css" };
import LANG from "../lang/lang.js";

const themeSheet = new CSSStyleSheet();
const componentSheet = new CSSStyleSheet();
themeSheet.replaceSync(themeCssStyle[0][1]);
componentSheet.replaceSync(componentCssStyle[0][1]);

/** @property {Object<Element>} coreElement 核心元素 */
/** @property {Object}  CoreData 核心数据 */
class ControlBox extends HTMLElement {
  coreElement = {};

  /** @property {Number} fixedType 定宽或者定高的类型，0表示定宽、1表示定高 */
  coreData = {
    fixedType: 0,
  };

  constructor() {
    super();
    this.#init();
  }

  #init() {
    const shadowRoot = this.attachShadow({ mode: "open" });
    const ele_template = document.querySelector(`.template_ControlBox`);
    const content = ele_template.content.cloneNode(true);
    // 获取核心元素，存储到coreElement属性中
    this.#language_init(content);
    // 自动控制区域
    const ele_input_autoWidth = content.querySelector(`#auto_width`);
    const ele_input_autoHeight = content.querySelector(`#auto_height`);
    const ele_input_size = content.querySelector(`.sizeInput`);
    const ele_btn_inputHandle = content.querySelector(`.btn_autoInput`);
    const ele_radio_fixedWidth = content.querySelector(`#fixedWidth`);
    const ele_radio_fixedHeight = content.querySelector(`#fixedHeight`);
    const ele_btn_autoCtrl = content.querySelector(`.btn_autoCtrl`);
    this.coreElement.ele_input_autoWidth = ele_input_autoWidth;
    this.coreElement.ele_input_autoHeight = ele_input_autoHeight;
    this.coreElement.ele_input_size = ele_input_size;
    this.coreElement.ele_btn_inputHandle = ele_btn_inputHandle;
    this.coreElement.ele_radio_fixedWidth = ele_radio_fixedWidth;
    this.coreElement.ele_radio_fixedHeight = ele_radio_fixedHeight;
    this.coreElement.ele_btn_autoCtrl = ele_btn_autoCtrl;

    // 手动控制区域
    const ele_input_manualWidth = content.querySelector(`.manual_width`);
    const ele_input_manualHeight = content.querySelector(`.manual_height`);
    const ele_btn_manualWidth_sub = content.querySelector(`.manualWidth_sub`);
    const ele_btn_manualWidth_add = content.querySelector(`.manualWidth_add`);
    const ele_btn_manualHeight_sub = content.querySelector(`.manualHeight_sub`);
    const ele_btn_manualHeight_add = content.querySelector(`.manualHeight_add`);
    this.coreElement.ele_input_manualWidth = ele_input_manualWidth;
    this.coreElement.ele_input_manualHeight = ele_input_manualHeight;
    this.coreElement.ele_btn_manualWidth_sub = ele_btn_manualWidth_sub;
    this.coreElement.ele_btn_manualWidth_add = ele_btn_manualWidth_add;
    this.coreElement.ele_btn_manualHeight_sub = ele_btn_manualHeight_sub;
    this.coreElement.ele_btn_manualHeight_add = ele_btn_manualHeight_add;

    // 自动控制 -  定宽、定高 单选框被选中事件
    ele_radio_fixedWidth.addEventListener("change", this.event_fixedType.bind(this), false);
    ele_radio_fixedHeight.addEventListener("change", this.event_fixedType.bind(this), false);
    // 自动控制 - 自动比例控制按钮点击
    ele_btn_autoCtrl.addEventListener("click", this.event_autoControl.bind(this), false);

    // 自动输入按钮 - 点击事件，根据用户输入的字符串自动分割填入宽度和高度输入框
    ele_btn_inputHandle.addEventListener("click", this.event_inputHandle.bind(this), false);

    // 手动控制 - 列宽调整通用按钮事件
    ele_btn_manualWidth_sub.addEventListener("click", this.event_manualWidth.bind(this), false);
    ele_btn_manualWidth_add.addEventListener("click", this.event_manualWidth.bind(this), false);
    // 手动控制 - 行高调整通用按钮事件
    ele_btn_manualHeight_sub.addEventListener("click", this.event_manualHeight.bind(this), false);
    ele_btn_manualHeight_add.addEventListener("click", this.event_manualHeight.bind(this), false);
    // 自动控制 / 手动控制 - 输入框滚轮滚动事件
    ele_input_autoWidth.addEventListener("wheel", this.event_manualNumber_wheel.bind(this), false);
    ele_input_autoHeight.addEventListener("wheel", this.event_manualNumber_wheel.bind(this), false);
    ele_input_manualWidth.addEventListener("wheel", this.event_manualNumber_wheel.bind(this), false);
    ele_input_manualHeight.addEventListener("wheel", this.event_manualNumber_wheel.bind(this), false);

    shadowRoot.adoptedStyleSheets = [themeSheet, componentSheet];
    shadowRoot.append(content);
  }

  /**
   * @function 中英双语支持字典表
   * @param {Element<Object>} param_element DOM元素
   */
  #language_init(param_element) {
    const ele_auto = param_element.querySelector(`.container .auto`);
    const ele_manual = param_element.querySelector(`.container .manual`);
    const elementList = {
      autoCtrl: ele_auto.querySelector(`.titleBar .title`),
      width: ele_auto.querySelector(`.control .attr_w`),
      height: ele_auto.querySelector(`.control .attr_h`),
      placeholder_size: ele_auto.querySelector(`.control .sizeInput`),
      placeholder_size_tips: ele_auto.querySelector(`.control .sizeInput`),
      place_autoInput: ele_auto.querySelector(`.control .btn_autoInput`),
      fix_width: ele_auto.querySelector(`.control .controlType .fixedWidth .title`),
      fix_height: ele_auto.querySelector(`.control .controlType .fixedHeight .title`),
      autoRatioCtrl: ele_auto.querySelector(`.control .btn_autoCtrl`),
      manualCtrl: ele_manual.querySelector(`.titleBar .title`),
      columnWidth: ele_manual.querySelector(`.control .title_w`),
      rowHeight: ele_manual.querySelector(`.control .title_h`),
    };

    // 检查当前语言环境，遍历LANG字典表，如果有匹配的则进行赋值，LANG字典表中的attr属性用于指定DOM元素的属性名
    // const currentLang = Office.context.displayLanguage;
    const currentLang = window.navigator.language;
    if (LANG[currentLang]) {
      LANG[currentLang].forEach((item) => {
        const ele = elementList[item["ele"]];
        if (item?.attr) {
          const attr = item["attr"];
          ele[attr] = item["str"];
        } else {
          ele.textContent = item["str"];
        }
      });
    }
    // 没有匹配的则使用默认值 - 中文
  }

  /**
   * @event 自动输入按钮 - 点击事件，根据用户输入的字符串自动分割填入宽度和高度输入框
   * @param {Event} e 事件源对象
   */
  async event_inputHandle(e) {
    e.stopPropagation();
    const userInput = this.coreElement.ele_input_size.value;
    const matchResult = /(\d+[.]*\d+)[^.]{1}(\d+[.]*\d+)/gi.exec(userInput);
    if (matchResult) {
      this.coreElement.ele_input_autoWidth.value = matchResult[1];
      this.coreElement.ele_input_autoHeight.value = matchResult[2];
    } else {
      this.coreElement.ele_input_size.value = "";
    }
  }

  /**
   * @event 自动控制/手动控制-输入框滚轮滚动事件
   * @param {Event} e 事件源对象
   */
  event_manualNumber_wheel(e) {
    e.stopPropagation();
    let value = parseFloat(e.target.value.trim());
    if (e.deltaY > 0) {
      e.target.value = value - 1;
    } else {
      e.target.value = value + 1;
    }
  }

  /**
   * @event 列宽调整通用按钮事件
   * @param {Event} e 事件源对象
   */
  async event_manualWidth(e) {
    // 阻止事件冒泡
    e.stopPropagation();

    // 获取用户输入框的值
    let value = this.coreElement.ele_input_manualWidth.value.trim();

    if (value == "") {
      this.coreElement.ele_input_manualWidth.focus();
      return;
    } else {
      value = parseFloat(value);
    }

    let FLAG_CTRL;
    // 区分控制的是加号还是减号
    if (e.target === this.coreElement.ele_btn_manualWidth_add) {
      FLAG_CTRL = true;
    } else if (e.target === this.coreElement.ele_btn_manualWidth_sub) {
      FLAG_CTRL = false;
    }

    await Excel.run(async (context) => {
      // 获取用户选择的范围
      const range = context.workbook.getSelectedRange();
      range.load(["width", "height", "rowCount", "columnCount", "isEntireColumn", "isEntireRow"]);
      await context.sync();
      // // 如果用户选择了整行或者整列则退出处理 - 弱判断，没有对用户选中的范围的每行或者每列进行判断
      // const checkEntireColumn = range.isEntireColumn;
      // const checkEntireRow = range.isEntireRow;
      // if (checkEntireColumn || checkEntireRow) {
      //   console.log(`用户选择了整行或者整列，退出处理`);
      //   return;
      // }

      // 提取所有列进行缓存、预读每一列的format属性，便于后续操作
      const range_cols = [];
      for (let i = 0; i < range.columnCount; i++) {
        const col = range.getColumn(i);
        col.load(["format"]);
        range_cols.push(col);
      }
      await context.sync();

      // 遍历所有列，调整列宽
      range_cols.forEach((col) => {
        // 调整列宽
        let value_target = 0;
        if (FLAG_CTRL == true) {
          // 预处理目标值，因为计算后的值可能是负数，如果值为0则强制设置为1
          value_target = col.format.columnWidth + value;
          if (value_target < 0) {
            value_target = 1;
          }
          col.format.columnWidth = value_target;
        } else if (FLAG_CTRL == false) {
          // 预处理目标值，因为计算后的值可能是负数，如果值为0则强制设置为1
          value_target = col.format.columnWidth - value;
          if (value_target < 0) {
            value_target = 1;
          }
          col.format.columnWidth = value_target;
        }
      });
    });
  }

  /**
   * @event 行高调整通用按钮事件
   * @param {Event} e 事件源对象
   */
  async event_manualHeight(e) {
    // 阻止事件冒泡
    e.stopPropagation();

    // 获取用户输入框的值
    let value = this.coreElement.ele_input_manualHeight.value.trim();

    if (value == "") {
      this.coreElement.ele_input_manualHeight.focus();
      return;
    } else {
      value = parseFloat(value);
    }

    let FLAG_CTRL;
    // 区分控制的是加号还是减号
    if (e.target === this.coreElement.ele_btn_manualHeight_add) {
      FLAG_CTRL = true;
    } else if (e.target === this.coreElement.ele_btn_manualHeight_sub) {
      FLAG_CTRL = false;
    }

    await Excel.run(async (context) => {
      // 获取用户选择的范围
      const range = context.workbook.getSelectedRange();
      range.load(["width", "height", "rowCount", "columnCount", "isEntireColumn", "isEntireRow"]);
      await context.sync();
      // // 如果用户选择了整行或者整列则退出处理 - 弱判断，没有对用户选中的范围的每行或者每列进行判断
      // const checkEntireColumn = range.isEntireColumn;
      // const checkEntireRow = range.isEntireRow;
      // if (checkEntireColumn || checkEntireRow) {
      //   console.log(`用户选择了整行或者整列，退出处理`);
      //   return;
      // }

      // 提取所有列进行缓存、预读每一列的format属性，便于后续操作
      const range_rows = [];
      for (let i = 0; i < range.rowCount; i++) {
        const row = range.getRow(i);
        row.load(["format"]);
        range_rows.push(row);
      }
      await context.sync();

      // 遍历所有列，调整列宽
      range_rows.forEach((row) => {
        // 调整列宽
        let value_target = 0;
        if (FLAG_CTRL == true) {
          // 预处理目标值，因为计算后的值可能是负数，如果值为0则强制设置为1
          value_target = row.format.rowHeight + value;
          if (value_target < 0) {
            value_target = 1;
          }
          row.format.rowHeight = value_target;
        } else if (FLAG_CTRL == false) {
          // 预处理目标值，因为计算后的值可能是负数，如果值为0则强制设置为1
          value_target = row.format.rowHeight - value;
          if (value_target < 0) {
            value_target = 1;
          }
          row.format.rowHeight = value_target;
        }
      });
    });
  }

  /**
   * @event 定宽/定高单选框点击事件
   * @param {Event} e 事件源对象
   */
  event_fixedType(e) {
    // 阻止事件冒泡
    e.stopPropagation();
    // 区分定宽或定高
    if (e.target === this.coreElement.ele_radio_fixedWidth) {
      this.coreData.fixedType = 0;
    } else if (e.target === this.coreElement.ele_radio_fixedHeight) {
      this.coreData.fixedType = 1;
    }
  }

  /**
   * @event 自动比例控制按钮点击事件处理函数
   * @param {Event} e 事件源对象
   */
  event_autoControl(e) {
    // 阻止事件冒泡
    e.stopPropagation();
    this.autoControl();
  }

  async autoControl() {
    // 获取用户输入的宽高，如果为空，则将焦点设置到对应的输入框
    const w = this.coreElement.ele_input_autoWidth.value.trim();
    const h = this.coreElement.ele_input_autoHeight.value.trim();
    if (w == "" || h == "") {
      if (w == "") {
        this.coreElement.ele_input_autoWidth.focus();
      } else if (h == "") {
        this.coreElement.ele_input_autoHeight.focus();
      }
      return;
    }
    try {
      await Excel.run(async (context) => {
        // 获取用户选择的范围
        const range = context.workbook.getSelectedRange();
        range.load(["width", "height", "rowCount", "columnCount", "isEntireColumn", "isEntireRow"]);
        await context.sync();
        // 如果用户选择了整行或者整列则退出处理 - 弱判断，没有对用户选中的范围的每行或者每列进行判断
        const checkEntireColumn = range.isEntireColumn;
        const checkEntireRow = range.isEntireRow;
        if (checkEntireColumn || checkEntireRow) {
          console.log(`用户选择了整行或者整列，退出处理`);
          return;
        }

        // 如果需要计算出像素大小 ( 像素单位: 需要*1.3333，此处可以用4/3来获取 )
        const range_w = range.width;
        const range_h = range.height;

        // 判断用户当前的要求是定宽还是定高
        // 定宽
        if (this.coreData.fixedType == 0) {
          // 计算出目标的总行高、总高度差、每行高度差
          const target_h = (range_w / w) * h;
          const dist_h_all = target_h - range_h;
          const dist_rowHeight = dist_h_all / range.rowCount;
          // console.log(`当前高度:${range_h} ${range_h * (4 / 3)}px`);
          // console.log(`目标高度:${target_h} ${target_h * (4 / 3)}px`);
          // 提取所有行进行缓存、预读每一行的format属性，便于后续操作
          const range_rows = [];
          for (let i = 0; i < range.rowCount; i++) {
            const row = range.getRow(i);
            row.load(["format"]);
            range_rows.push(row);
          }
          // 遍历每一行进行设置行高: 提取当前行高、转换为像素单位、计算目标行高、设置行高
          await context.sync();
          range_rows.forEach((row, index) => {
            // console.log(`第${index + 1}行，${(row.format.rowHeight + dist_rowHeight) * (4 / 3)}px`);
            row.format.rowHeight = row.format.rowHeight + dist_rowHeight;
          });
        } else if (this.coreData.fixedType == 1) {
          // 计算出目标的总列宽、总列宽差、每列宽度差
          const target_w = (range_h / h) * w;
          const dist_w_all = target_w - range_w;
          const dist_colWidth = dist_w_all / range.columnCount;
          // console.log(`当前宽度:${range_w} ${range_w * (4 / 3)}px`);
          // console.log(`目标宽度:${target_w} ${target_w * (4 / 3)}px`);
          // 提取所有列进行缓存、预读每一列的format属性，便于后续操作
          const range_cols = [];
          for (let i = 0; i < range.columnCount; i++) {
            const col = range.getColumn(i);
            col.load(["format"]);
            range_cols.push(col);
          }
          // 遍历每一列进行设置列宽: 提取当前列宽、转换为像素单位、计算目标列宽、设置列宽
          await context.sync();
          range_cols.forEach((col) => {
            col.format.columnWidth = col.format.columnWidth + dist_colWidth;
          });
          await context.sync();
        }
      });
    } catch (err) {
      console.log(err);
    }
  }
}
export default ControlBox;
